.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.main{
    background: white;
    height: 300px;
    width: 200px;
    border-radius: 50%;
    border: 5px solid black;
}

.number{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 50px;
    font-weight: bolder;
}

.calcs{
    display: flex;
    justify-content: space-between;
    gap: 120px;
}

.inner-calcs{
    background: #F2B15E;
    color: #ffffff;
    height: 80px;
    width: 65px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;
    margin-top: -120px;
    border: 3px solid black;
    font-weight: bold;
    cursor: pointer;
}